    display: block;
  }

  #toc-contents ul {
    float: none;
    padding: 30px 0 0 0 !important;
  }

  #toc-contents {
    padding: 0;
    height: 400px;
    margin-top: 180px;
    width: 0;
    padding-bottom: 10px;
    padding-top: 50px;
    position: fixed;
    z-index: 1;
    box-shadow: -5px 8px 20px 0px $rwth-blue-color !important;
    top: 0;
    text-align: left;
    border-right: none;
    right: 0;
    color: $rwth-blue-color;
    background-color: #fff;
    overflow-x: hidden;
    overflow-y: scroll;
    transition: 0.5s;
  }


  #toc-contents a {
    padding-left: 32px;
    padding-bottom: 5px;
    text-decoration: none;
    font-size: 15px;
    color: #818181;
    display: block;
    transition: 0.3s;
  }

  #toc-contents ul li a {
    line-height: 10px;
  }

  br {
    display: none;
  }

  #toc-contents a:hover {
    color: $rwth-blue-color;
  }

  #toc-contents .toc-closebtn {
    position: absolute;
    top: 0;
    font-size: 34px;
    line-height: 1;
  }

  #toc-contents .toc-closebtn:hover {
    color: $rwth-blue-color;
  }

  .toc-openbtn {
    z-index: 111;
    top: 125px;
    position: fixed;
    box-shadow: 0 4px 9px -4px $rwth-blue-color !important;
    right: 0;
    font-size: 15px;
    cursor: pointer;
    background-color: $rwth-blue-color;
    color: #fff;
    padding: 8px;
    border: none;
  }

  .toc-openbtn:hover {
    background-color: $rwth-blue-color;
  }


}

.toc__list:before {
  content: "Table Of Contents";
  font-size: 13px;
  margin-left: -9px;
}

.toc__list {
  line-height: 1.8rem;
  font-size: 12px;
  line-height: 19px;
  padding: 9px;
}

.toc__list li {
  list-style-type: '►';
}

.toc__list li a {
  padding: 10px;
}

.toc__sublist {
  padding-left: 20px;
  display: none;
}

.toc__sublist li {
  list-style-type: '◦';
  list-style-position: inside;
}

.toc__sublist li a {
  padding: 10px !important;
}

.toc-h2:first-child {
  margin-top: 1rem;
}

#toc a:hover {
  color: $rwth-red-color;
}

// Scroll to Top Button

html {
  scroll-behavior: smooth;
}

#btn-back-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: none;
  width: 2.8125rem;
  height: 2.8125rem;
  cursor: pointer;
  border-radius: 50%;
  padding: 0;
  font-size: .875rem;
  line-height: 1.6;
  background-color: $rwth-blue-color !important;
  color: #fff;
  box-shadow: 0 4px 9px -4px $rwth-blue-color !important;
  font-weight: 500;
  text-transform: uppercase;
  vertical-align: bottom;
  border: 0;
  text-align: center;
  text-decoration: none;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.emb-monti-arc {
  border: 1px solid #111;
  padding: 10px;
}

// ThemenWaben component
.themenwaben-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 40px;
}

.themenwaben-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  width: 200px;

  &:hover {
    text-decoration: none;

    .themenwaben-image-wrapper {
      transform: scale(1.05);
      filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.2));
    }

    .themenwaben-text {
      color: var(--global-theme-color);
    }
  }
}

.themenwaben-image-wrapper {
  position: relative;
  width: 200px;
  height: 174px;
  transition: transform 0.3s ease, filter 0.3s ease;
}

.themenwaben-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.themenwaben-text {
  margin-top: 15px;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 500;
  color: var(--global-text-color);
  transition: color 0.3s ease;
}


// Promotion Section
.postdoc-section {
  padding-left: 0;
  border-left: none;
  border-top: 1px solid $rwth-lighter-blue-color;
  padding-top: 30px;
  margin-top: 10px;
}

@media (min-width: 768px) {
  .postdoc-section {
    padding-left: 40px !important;
    border-left: 1px solid $rwth-lighter-blue-color !important;
    border-top: none !important;
    padding-top: 0 !important;
    margin-top: 0 !important;
  }
}

.job-button {
  display: inline-block;
  background-color: $rwth-blue-color;
  color: white !important;
  padding: 10px 20px;
  border-radius: 4px;
  text-decoration: none !important;
  font-weight: bold;
  margin-right: 15px;
  transition: all 0.2s ease-in-out;
}

.job-button:hover {
  background-color: $rwth-light-blue-color;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
}

.promotion-container {
  background-color: $rwth-lightest-blue-color;
  padding: 50px 40px;
  border-radius: 8px;
  margin: 40px 0;
  border-left: 6px solid $rwth-blue-color;
}

.promotion-title {
  text-align: left;
  margin-top: 0;
}

.promotion-flex {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}

.promotion-column {
  flex: 1;
  min-width: 300px;
}

.promotion-subtitle {
  color: $rwth-blue-color;
  margin-top: 0;
  font-size: 1.4em;
}

.promotion-text {
  color: #333;
  line-height: 1.6;
}

.promotion-button-container {
  margin-top: 25px;
}

.promotion-small-text {
  margin-top: 15px;
  font-size: 0.9em;
  color: #555;
}

.promotion-link {
  color: $rwth-blue-color;
  text-decoration: underline;
}

.promotion-italic {
  font-style: italic;
}

.promotion-link-bold {
  color: $rwth-blue-color;
  font-weight: bold;
  text-decoration: none;
}


// Hero Section
.hero-banner {
  background-size: cover;
  background-position: center right;
  background-repeat: no-repeat;
  width: 100%;
  min-height: 480px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 60px;
  margin-top: -3rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  background-image: linear-gradient(to right, $rwth-blue-color 0%, $rwth-blue-color 40%, rgba($rwth-blue-color, 0.5) 100%), var(--hero-bg-image);

  @media (max-width: 768px) {
    background-image: linear-gradient(rgba($rwth-blue-color, 0.9), rgba($rwth-blue-color, 0.9)), var(--hero-bg-image);
    background-position: center;
  }
}

.hero-container {
  display: flex;
  align-items: center;
  height: 100%;
}

.hero-content {
  max-width: 650px;
  text-align: left;
  color: white;
}

.hero-title {
  color: white;
  margin-bottom: 20px;
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 1.1;
}

.hero-description {
  color: white;
  font-size: 1.25rem;
  margin-bottom: 35px;
  opacity: 0.95;
  font-weight: 300;
  line-height: 1.6;
}

.mission-video {
  flex: 1 1 400px;
  aspect-ratio: 16/9;
  overflow: hidden;
}
